body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;
  cursor: default;
  color: #661e1e; }

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-size: 0.875em;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 400;
  box-sizing: inherit; }

#root {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box; }

.appContainer {
  border-bottom: 2px inset #a3a3a3;
  background-color: #E0E0E0;
  width: 100%;
  height: 100vh;
  overflow: auto; }

.appMenu {
  display: block;
  position: fixed;
  text-align: right;
  width: 100%;
  top: -0.5rem;
  padding: 1rem; }

.listItem {
  min-height: 20vh;
  max-height: 20vh;
  padding: 0.5rem;
  text-align: center;
  display: flex;
  margin: 0.9rem;
  background: #fefefe;
  border-radius: .5rem; }
  .listItem__pic {
    width: 30%;
    max-width: 30%;
    height: 100%; }
    .listItem__pic img {
      display: block;
      height: 100%;
      max-height: calc(20vh - 1rem);
      width: auto;
      height: auto;
      border-radius: 0.5rem; }
  .listItem__name {
    margin: auto;
    width: 70%;
    max-width: 70%;
    font-size: 1.5rem; }

.mainMenu__checkbox {
  display: none; }

.mainMenu__button {
  background-color: white;
  height: 2.2rem;
  width: 2.2rem;
  position: fixed;
  top: 1rem;
  right: 1rem;
  border-radius: 50%;
  z-index: 2000;
  box-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.1);
  text-align: center; }

.mainMenu__background {
  height: 2.2rem;
  width: 2.2rem;
  border-radius: 50%;
  position: fixed;
  top: 1rem;
  right: 1rem;
  background-image: -webkit-radial-gradient(#f1efed, #f0f0f0);
  background-image: radial-gradient(#f1efed, #f0f0f0);
  z-index: 1000;
  -webkit-transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1); }

.mainMenu__nav {
  height: 100vh;
  width: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  -webkit-transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  background: rgba(255, 255, 255, 0.5); }

.mainMenu__list {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
  width: 0;
  opacity: 0; }

.mainMenu__item {
  margin: 2rem;
  border-radius: 3px;
  border: 1px solid #b1a79d; }

.mainMenu__link {
  font-size: 2rem;
  font-weight: 300;
  border: none;
  text-decoration: none;
  background: none;
  outline: none; }
  .mainMenu__link--selected {
    font-weight: bolder; }

.mainMenu__checkbox:checked ~ .mainMenu__background {
  -webkit-transform: scale(80);
          transform: scale(80);
  opacity: 0.8; }

.mainMenu__checkbox:checked ~ .mainMenu__nav {
  opacity: 1;
  width: 100%; }

.mainMenu__checkbox:checked ~ .mainMenu__nav > .mainMenu__list {
  opacity: 1;
  width: 100%; }

.mainMenu__icon {
  position: relative;
  margin-top: 1rem; }
  .mainMenu__icon, .mainMenu__icon::before, .mainMenu__icon::after {
    width: 1rem;
    height: 2px;
    background-color: #333333;
    display: inline-block; }
  .mainMenu__icon::before, .mainMenu__icon::after {
    content: '';
    position: absolute;
    left: 0;
    -webkit-transition: all 0.2s;
    transition: all 0.2s; }
  .mainMenu__icon::before {
    top: -0.4rem; }
  .mainMenu__icon::after {
    top: 0.4rem; }

.mainMenu__checkbox:checked + .mainMenu__button .mainMenu__icon {
  background-color: transparent; }

.mainMenu__checkbox:checked + .mainMenu__button .mainMenu__icon::before {
  top: 0;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg); }

.mainMenu__checkbox:checked + .mainMenu__button .mainMenu__icon::after {
  top: 0;
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg); }

.drinkItem {
  border-radius: 13px 13px 13px 13px;
  border: 4px solid #d3d3d3;
  background-color: #f7f7f7;
  height: calc(100vh - 2px);
  width: 100%;
  display: grid;
  grid-column: 1fr;
  grid-template-rows: 1fr 3rem;
  flex-direction: column;
  box-shadow: 0px 0px 6px -1px #787171 inset; }
  .drinkItem__body {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 4rem 1fr; }
    .drinkItem__body__banner {
      display: grid;
      grid-template-columns: 3rem 1fr;
      font-size: 1.5rem;
      text-align: center;
      line-height: 4rem; }
      .drinkItem__body__banner :nth-child(2) {
        text-align: left; }
    .drinkItem__body__content {
      overflow-y: scroll;
      max-height: calc( 100vh - 2px - 4rem - 3rem); }
      .drinkItem__body__content__image img {
        margin-top: 0.3rem;
        box-shadow: 0px 0px 6px 2px #463838;
        border-radius: 10px;
        max-width: 85vw;
        width: auto;
        height: auto;
        display: block;
        margin-left: auto;
        margin-right: auto; }
      .drinkItem__body__content__image--lazy {
        width: 80vw;
        height: auto;
        background-size: contain; }
      .drinkItem__body__content__price {
        font-size: 1.2rem;
        margin: 2rem;
        display: flex;
        flex-direction: column; }
        .drinkItem__body__content__price div {
          display: flex;
          margin-top: 0.2rem;
          flex-direction: row;
          justify-content: space-around;
          border-bottom: 2px solid #d3d3d3; }
      .drinkItem__body__content__topping {
        border-top: 1px solid #333;
        padding-top: 1rem;
        margin: 2rem;
        font-size: 1.2rem;
        text-align: center; }
        .drinkItem__body__content__topping :nth-child(2) {
          width: 100%; }
          .drinkItem__body__content__topping :nth-child(2) table {
            margin: 0 1rem;
            padding-top: 1rem; }
      .drinkItem__body__content__detail {
        font-size: 1.2rem;
        margin: 2rem;
        border-top: 1px solid #333;
        padding-top: 1rem; }
  .drinkItem__footer {
    display: flex;
    justify-content: space-around; }
    .drinkItem__footer--back {
      width: 3rem; }
    .drinkItem__footer--home {
      border-radius: 3px;
      border: 1px solid #b1a79d;
      display: inline-block;
      margin-bottom: 0.8rem;
      padding: 6px 24px;
      text-decoration: none;
      font-size: 1.2rem;
      text-shadow: 0px 1px 0px #4d3534; }
    .drinkItem__footer--forward {
      width: 3rem; }

